@import url("https://fonts.googleapis.com/css?family=IBM+Plex+Sans&display=swap");
.register-btn {
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 1rem;
  border-width: 0;
  box-shadow: 0 0 0.1rem 0.001rem #999;
  color: black;
  font-size: 14px;
  letter-spacing: 2px;
  padding: 10px 16px;
  text-decoration: none;
  transition: 0.3s; }
  .register-btn:hover {
    background-color: white;
    box-shadow: 0 0 0.3rem 0.001rem #999;
    color: #880e4f; }
  .register-btn:visited {
    color: black; }
    .register-btn:visited:hover {
      color: #880e4f; }

.login-btn {
  background-color: rgba(255, 255, 255, 0);
  border-color: rgba(255, 255, 255, 0.85);
  border-radius: 1rem;
  border-style: solid;
  border-width: 2px;
  color: rgba(255, 255, 255, 0.85);
  font-size: 14px;
  letter-spacing: 2px;
  padding: 8px 14px;
  text-decoration: none;
  transition: 0.3s; }
  .login-btn:hover {
    background-color: rgba(255, 255, 255, 0.2); }
  .login-btn:visited {
    color: rgba(255, 255, 255, 0.85); }

.primary-btn {
  background-color: #880e4f;
  border-radius: 0.4rem;
  border-width: 0px;
  box-shadow: 0 0 0.1rem 0.0001rem #999;
  color: #f0f0f0;
  letter-spacing: 3px;
  margin: 20px 10px;
  padding: 12px 22px;
  text-decoration: none;
  transition: 0.3s; }
  .primary-btn:hover {
    background-color: #bc477b;
    box-shadow: 0 0 0.3rem 0.01rem #999; }
  .primary-btn:visited {
    color: #f0f0f0; }

.clear-btn {
  background-color: rgba(0, 0, 0, 0.5);
  border-width: 0;
  border-color: rgba(0, 0, 0, 0);
  color: white;
  cursor: pointer;
  margin: 0;
  padding: 0; }

.footer {
  align-items: flex-end;
  background-color: rgba(0, 0, 0, 0);
  border-width: 0px;
  color: white;
  display: flex;
  flex-wrap: wrap;
  font-family: "IBM Plex Sans", sans-serif;
  justify-content: space-between;
  padding: 1rem 2rem 2rem 2rem; }
  .footer a {
    color: #f0f0f0;
    padding: 0px;
    text-decoration: none; }
    .footer a:visited {
      color: #f0f0f0; }
  .footer ul {
    margin: 0.8rem;
    padding: 0;
    list-style: none; }
  .footer li {
    margin: 0.5rem 0; }

.flex_group {
  align-items: center;
  display: inline-flex;
  height: 100%;
  margin: 0 1rem; }

.logo {
  margin: 10px;
  display: inline-block; }
  .logo img {
    position: relative;
    text-align: center; }

.nav-links {
  display: flex;
  transition: 0.3s; }
  .nav-links li {
    list-style: none;
    padding: 10px;
    transition: 0.3s; }

.footer_icon {
  color: white;
  font-size: 28px;
  transition: 0.3s; }
  .footer_icon:hover {
    color: #ffe170; }

.footer_link {
  color: white;
  font-size: 20px;
  transition: 0.3s; }
  .footer_link:hover {
    color: #ffe170; }

@media only screen and (max-width: 700px) {
  .footer {
    justify-content: space-between;
    padding: 1rem 0 0 0; } }

.logo-link {
  font-weight: bold;
  letter-spacing: 4px; }
  .logo-link a {
    color: white;
    border-bottom: 3px solid white;
    text-decoration: none; }
    .logo-link a:visited {
      color: white; }

.profile-group {
  display: inline-block;
  position: relative; }
  .profile-group:hover .profile_pic_options {
    display: block; }
  .profile-group:hover .profile_pic {
    background-color: #3e8e41; }

.profile-pic {
  background-color: #880e4f;
  border-radius: 50%;
  color: white;
  cursor: pointer;
  display: inline-block;
  width: 50px;
  position: relative;
  z-index: 1; }

.profile-pic-options {
  background-color: #fdfdfd;
  border-radius: 0.4rem;
  box-shadow: 0 0.2rem 0.4rem 0.01rem gray;
  display: none;
  position: absolute;
  left: auto;
  right: 0;
  top: 20;
  z-index: 100; }
  .profile-pic-options a {
    border-radius: 0.4rem;
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block; }
    .profile-pic-options a:hover {
      background-color: #f0f0f0; }
