.non-hamburger-menu {
  align-items: flex-start;
  display: flex;
  flex-wrap: nowrap; }

@media only screen and (max-width: 700px) {
  .non-hamburger-menu {
    align-items: flex-start;
    display: none;
    flex-wrap: nowrap; } }

.hamburger-menu {
  display: none;
  cursor: pointer;
  overflow: visible; }
  .hamburger-menu ul {
    float: right; }
    .hamburger-menu ul li {
      color: white; }

@media only screen and (max-width: 700px) {
  .hamburger-menu {
    display: block; } }

.hamburger-button {
  background-color: rgba(0, 0, 0, 0);
  border-bottom-color: white;
  border-style: none;
  cursor: pointer;
  display: none;
  margin: 0;
  padding: 0;
  transition: 300ms; }
  .hamburger-button:hover {
    border-bottom-color: #ffe170;
    background-color: rgba(255, 255, 255, 0.2); }

@media only screen and (max-width: 700px) {
  .hamburger-button {
    display: block;
    cursor: pointer;
    float: right; } }

.bar1, .bar2, .bar3 {
  border-bottom-color: inherit;
  border-bottom-style: solid;
  border-bottom-width: 3px;
  border-radius: 0;
  width: 32px;
  margin: 0.4rem 0 0;
  padding: 0;
  transition: 300ms; }
