@import url("https://fonts.googleapis.com/css?family=IBM+Plex+Sans&display=swap");
.article {
  border-color: #999;
  border-radius: 6px;
  border-style: solid;
  border-width: 0px;
  box-shadow: 0 0 0.35rem 0.001rem #444;
  margin: 0rem 2rem; }

@media only screen and (max-width: 700px) {
  .article {
    border-color: #999;
    border-radius: 6px;
    border-style: solid;
    border-width: 0px;
    box-shadow: 0 0 0.35rem 0.001rem #444;
    margin: 0; } }

.article-topbar {
  background-color: rgba(251, 251, 251, 0.25);
  display: flex;
  font-family: "IBM Plex Sans", sans-serif;
  justify-content: space-between;
  margin: 0; }
  .article-topbar a {
    color: white;
    background-color: rgba(0, 0, 0, 0);
    padding: 1rem;
    text-decoration: none;
    transition: 0.3s; }
    .article-topbar a:hover {
      background-color: rgba(0, 0, 0, 0.25); }
    .article-topbar a:visited {
      color: white; }

@media only screen and (max-width: 700px) {
  .article-topbar a {
    padding: 0.7rem; } }

.article-topbar-link {
  background-color: #fbfbfb;
  padding: 0.7rem;
  text-align: center; }

.article-content {
  background-color: #fbfbfb;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 18px;
  padding: 1rem 3rem 2rem 3rem; }
  .article-content h1 {
    border-bottom: 3px solid black;
    font-size: 48px;
    font-weight: bold;
    margin: 2rem 0 1rem 0; }
  .article-content h2 {
    font-size: 40px;
    font-weight: bold;
    margin: 2rem 0 0 0; }
  .article-content h3 {
    font-size: 36px;
    font-weight: bold;
    margin: 0.4rem 0 0 0; }
  .article-content h4 {
    font-size: 30px;
    margin: 0; }
  .article-content h5 {
    margin: 0;
    font-size: 26px; }
  .article-content h6 {
    font-size: 20px;
    margin: 0;
    font-weight: normal; }
  .article-content p {
    font-size: 18px; }
  .article-content li {
    font-size: 18px; }
  .article-content table {
    background-color: #880e4f;
    border-collapse: collapse;
    box-shadow: 0 0 0.4rem 0.001rem #777;
    display: inline-block;
    max-width: 100%;
    overflow-x: auto;
    white-space: nowrap;
    width: auto; }
  .article-content thead tr {
    background-color: rgba(0, 0, 0, 0.1); }
    .article-content thead tr:hover {
      background-color: rgba(0, 0, 0, 0); }
  .article-content th {
    color: white;
    padding: 10px; }
  .article-content tr {
    background-color: rgba(255, 255, 255, 0.4);
    color: white;
    padding: 10px; }
    .article-content tr:hover {
      background-color: rgba(255, 255, 255, 0.1); }
    .article-content tr:nth-child(even) {
      background-color: rgba(255, 255, 255, 0.3); }
      .article-content tr:nth-child(even):hover {
        background-color: rgba(255, 255, 255, 0.1); }
    .article-content tr:last-child {
      border-radius: 8px 0px 0px 8px; }
  .article-content td {
    padding: 10px; }

@media only screen and (max-width: 700px) {
  .article-content {
    background-color: #fbfbfb;
    font-size: 16px;
    padding: 0.3rem 1.1rem 2rem 1.1rem; }
    .article-content h1 {
      border-bottom: 2px solid black;
      font-size: 26px;
      font-weight: bold; }
    .article-content h2 {
      font-size: 24px;
      font-weight: bold; }
    .article-content h3 {
      font-size: 22px;
      font-weight: bold; }
    .article-content h4 {
      font-size: 20px;
      margin: 0; }
    .article-content h5 {
      font-size: 20px;
      margin: 0; }
    .article-content h6 {
      font-size: 16px;
      margin: 0; }
    .article-content p ul {
      font-size: 16px; }
    .article-content ul {
      padding: 0 1rem 0 1.5rem; } }

.article-icon {
  color: black;
  display: inline;
  font-size: 32px;
  transition: 0.3s; }
  .article-icon:hover {
    color: purple; }
